.rating {
  display: flex;
  align-items: center;
  margin-bottom: rem(40);
  .star {
    &:not(:last-child) {
      margin-right: rem(2);
    }
      svg {
        fill: #000;
      }
  }
  &__text {
    display: inline-flex;
    align-items: center;
    margin-top: 2px;
    margin-left: rem(5);
  }
}

.star {
  display: inline-flex;
  align-items: center;
  justify-content: center;
}
