.catalog {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -15px;
  &__col {
    padding: 0 15px;
    &--product {
      width: 75%;
    }
    &--filter {
      width: 25%;
    }
  }
}

.catalog-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(243px, 1fr));
  gap: rem(20);
}

.product {
  position: relative;
  margin-bottom: rem(20);
  &__img {
    position: relative;
    margin-bottom: rem(6);
    overflow: hidden;
    a {
      display: block;
      width: 100%;
      height: 100%;
    }
    img {
      width: 100%;
    }
  }
  &__price-rating {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: rem(3);
    color: var(--primary-color);
  }
  &__price {
    margin-right: rem(15);
    font-size: rem(16);
    font-weight: 700;
    color: var(--text-color);
    white-space: nowrap;
    &--old {
      font-size: rem(14);
      font-weight: 400;
      text-decoration: line-through;
      color: var(--text-color);
    }
    &--now {
      color: var(--primary-color);
    }
  }
  &__rating {
    display: flex;
    align-items: center;
    line-height: 125%;
    .star {
      margin-left: rem(2);
      svg {
        fill: var(--primary-color);
      }
    }
  }
  &__title {
    margin-bottom: rem(3);
    line-height: 120%;
    a {
      transition: color .2s ease;
      &:hover {
        color: var(--primary-color);
      }
    }
  }
  &__deliv {
    display: flex;
    align-items: center;
    color: #909090;
  }
  &__remark {
    display: inline-flex;
    margin-top: rem(8);
    padding: .25em .4166666666666667em;
    font-size: rem(12);
    background-color: #ffe0e0;
    border-radius: 50px;
    &--green {
      background-color: #cff1df;
    }
  }
}

.bus {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: rem(5);
}

.filter {
  margin-left: rem(35);
}

.filter-block {
  margin-bottom: rem(30);
  &__title {
    font-size: rem(14);
    font-weight: 700;
    margin-bottom: 1.0714285714285714em;
  }
}

.filter-list {
  max-height: 270px;
  padding-left: 0;
  overflow: auto;
  &__item {
    display: flex;
    align-items: center;
    margin-bottom: rem(10);
    transition: all .2s ease-in-out;
    &:hover {
      .filter-list__text {
        color: var(--primary-color);
        &::before {
          border-color: var(--primary-color);
        }
      }
    }
  }
  &__radio {
    display: none;
    width: 14px;
    height: 14px;
    margin-right: rem(10);
  }
  &__text {
    position: relative;
    padding-left: 25px;
    font-size: rem(14);
    font-weight: 300;
    cursor: pointer;
    user-select: none;
    &::before {
      content: "";
      display: inline-block;
      width: 14px;
      height: 14px;
      position: absolute;
      left: 0;
      bottom: 1px;
      border: 1px solid #939fae;
      border-radius: 50%;
    }
    &:hover {
      color: var(--primary-color);
    }
  }
}
.filter-list__item .filter-list__radio:checked + .filter-list__text::before {
  border-color: var(--primary-color);
  background: url('../img/ellipse.svg');
  background-repeat: no-repeat;
  background-position: 50% 50%;
}

