@media (max-width: (992px)) {
  .container {
    padding: 0 15px;
  }
  .catalog-grid {
    grid-template-columns: 1fr 1fr;
  }
  .categories-overflow {
    padding-bottom: rem(8);
  }
  .catalog {
    &__col {
      &--product {
        width: 100%;
      }
      &--filter {
        display: none;
        width: 100%;
      }
    }
  }
}

@media (max-width: (576px)) {
  .title-h1 {
    font-size: rem(24);
  }
  .title-h2 {
    font-size: rem(20);
  }
  .rating {
    margin-bottom: rem(20);
    &__text {
      font-size: rem(14);
    }
  }
  .catalog-grid {
    gap: rem(15);
  }
  .product {
    &__price {
      font-size: rem(14);
      &--old {
        font-size: rem(12);
      }
    }
    &__title {
      font-size: rem(14);
    }
    &__rating {
      font-size: rem(12);
    }
    &__deliv {
      font-size: rem(12);
    }
  }
  .star {
    svg {
      width: 14px;
      height: 14px;
    }
  }
  .bus {
    width: 16px;
    height: 8px;
  }
}
