.categories-wrapper {
  padding: rem(12) 0;
  border-top: 1px solid #eaedf3;
  border-bottom: 1px solid #eaedf3;
}

.categories-overflow {
  max-width: 100%;
  overflow-x: auto;
}

.categories-list {
  display: flex;
  align-items: center;
  padding-left: 0;
  &__link {
    display: inline-flex;
    align-items: center;
    padding: rem(9) rem(12);
    white-space: nowrap;
    background-color: var(--second-color);
    border-radius: 4px;
  }
  &__item {
    margin-right: rem(8);
  }
}

.active {
  color: var(--light-color);
  background-color: #20201f;
}
